import
electron,
{
	BrowserWindow,
	app as ElectronApp,
	Menu,
	ipcMain
}
	from "electron";
import Lang from "../../lang";
var fs = require("fs-extra");
var os = require("os");
const { shell } = require("electron");
const { autoUpdater } = require("electron-updater");

const feedUrl = `https://download-sdk.oss-cn-beijing.aliyuncs.com/mp/${process.platform}`;

const IS_MAC_OSX = process.platform === "darwin";

if(DEBUG && process.type === "renderer"){
	console.error("AppRemote must run in main process.");
}

class AppRemote {
	constructor(){
		// 
		var dir = `${os.homedir()}/easemob`;
		fs.ensureDir(dir, function(err){
			console.log(err);
		});

		this.windows = {};

		ipcMain.on("open-file", (e, filePath) => {
			shell.showItemInFolder(filePath);
		});

		// 
		ipcMain.on("receive-unread-msg", (e, badge) => {
			if(process.platform == "darwin"){
				if(badge > 0){
					ElectronApp.dock.setBadge(badge.toString());
				}
				else if(badge > 99){
					ElectronApp.dock.setBadge("99+");
				}
				else{
					ElectronApp.dock.setBadge("");
				}
			}
			else if(process.platform === "win32"){
				if(badge > 0){
					ElectronApp.setBadgeCount(badge.toString());
				}
				else if(badge > 99){
					ElectronApp.setBadgeCount("99+");
				}
				else{
					ElectronApp.setBadgeCount("");
				}

			}

		});

	}

	init(entryPath){
		if(!entryPath){
			throw new Error("Argument entryPath must be set on init app-remote.");
		}
		this.entryPath = entryPath;
	}

	ready(){
		this.openMainWindow();
	}

	openMainWindow(){
		const mainWindow = this.mainWindow;
		if(!mainWindow){
			this.createMainWindow();
		}
		else if(!mainWindow.isVisible()){
			mainWindow.show();
			mainWindow.focus();
		}
	}

	createMainWindow(options){
		options = Object.assign({
			width: 900,
			height: 650,
			minWidth: 900,
			minHeight: 650,
			url: "index.html",
			hashRoute: "/index",
			name: "main",
			resizable: true,
			debug: true
		}, options);

		if(DEBUG){
			const display = electron.screen.getPrimaryDisplay();
			options.height = display.workAreaSize.height;
			options.width = 800;
			options.x = display.workArea.x;
			options.y = display.workArea.y;
		}
		this.mainWindow = this.createWindow(options);
	}

	createWindow(options){
		var name;
		if(typeof options === "object"){
			name = options.name;
		}

		options = Object.assign({
			name,
			showAfterLoad: true,
			hashRoute: `/${name}`,
			url: "index.html",
			autoHideMenuBar: !IS_MAC_OSX,
			backgroundColor: "#465d78",
			show: DEBUG,
			frame: true,
			titleBarStyle: "hidden",
			webPreferences: { webSecurity: false },
			thickFrame: true,
		}, options);

		let browserWindow = this.windows[name];
		if(browserWindow){
			throw new Error(`The window with name '${name}' has already be created.`);
		}

		const windowSetting = Object.assign({}, options);
		[
			"url",
			"showAfterLoad",
			"debug",
			"hashRoute",
			"onLoad",
			"beforeShow",
			"afterShow",
			"onClosed"
		].forEach((optionName) => {
			delete windowSetting[optionName];
		});
		browserWindow = new BrowserWindow(windowSetting);

		this.windows[name] = browserWindow;

		// browserWindow.on("closed", () => {
		// 	delete this.windows[name];
		// 	browserWindow = null;
		// });

		browserWindow.webContents.on("did-finish-load", () => {
			if(options.showAfterLoad){
				if(options.beforeShow){
					options.beforeShow(browserWindow, name);
				}
				browserWindow.show();
				browserWindow.focus();
				if(options.afterShow){
					options.afterShow(browserWindow, name);
				}
			}
			if(options.onLoad){
				options.onLoad(browserWindow);
			}
		});

		let url = options.url;
		if(url){
			if(!url.startsWith("file://") && !url.startsWith("http://") && !url.startsWith("https://")){
				url = `file://${this.entryPath}/${options.url}`;
			}
			if(DEBUG){
				url += "?react_perf";
			}
			if(options.hashRoute){
				url += `#${options.hashRoute}`;
			}
			browserWindow.loadURL(url);
		}

		if(options.debug){
			// browserWindow.openDevTools();
			browserWindow.webContents.on("context-menu", (e, props) => {
				const { x, y } = props;
				Menu.buildFromTemplate([{
					label: Lang.string("debug.inspectElement"),
					click(){
						browserWindow.inspectElement(x, y);
					}
				}]).popup(browserWindow);
			});
		}

		// 
		let sendUpdateMessage = (message, data) => {
			browserWindow.webContents.send("message", { message, data });
		};

		let checkForUpdates = () => {
			autoUpdater.setFeedURL(feedUrl);

			autoUpdater.on("error", function(message){
				sendUpdateMessage("error", message);
			});
			autoUpdater.on("checking-for-update", function(message){
				sendUpdateMessage("checking-for-update", message);
			});
			autoUpdater.on("update-available", function(message){
				sendUpdateMessage("update-available", message);
			});
			autoUpdater.on("update-not-available", function(message){
				sendUpdateMessage("update-not-available", message);
			});

			// 
			autoUpdater.on("download-progress", function(progressObj){
				sendUpdateMessage("downloadProgress", progressObj);
			});
			autoUpdater.on("update-downloaded", function(event, releaseNotes, releaseName, releaseDate, updateUrl, quitAndUpdate){
				ipcMain.on("updateNow", (e, arg) => {
					// some code here to handle event
					autoUpdater.quitAndInstall();
				});
				sendUpdateMessage("isUpdateNow", event, releaseNotes, releaseName, releaseDate, updateUrl, quitAndUpdate);
			});

			// 
			autoUpdater.checkForUpdates();
		};

		setTimeout(checkForUpdates, 1000);

		return browserWindow;
	}


	closeMainWindow(){
		this.markClose = true;
		const mainWindow = this.mainWindow;
		if(mainWindow){
			mainWindow.close();
		}
	}

	quit(){
		this.closeMainWindow();
		ElectronApp.quit();
	}
}

const app = new AppRemote();
export default app;



